<template>
    <el-row>
        <el-col :span="16">
            <el-form id="siteForm" :rules="rules" ref="form" :model="form" label-width="auto" :status-icon="true">
                <el-form-item label="" prop="siteName">
                    <el-input v-model="form.siteName"/>
                </el-form-item>

                <el-form-item label="" prop="domain">
                    <el-input v-model="form.domain"/>
                </el-form-item>

                <el-form-item label="">
                    <el-switch v-model="form.infoEnable"/>
                    <span class="zfile-word-aux"></span>
                </el-form-item>

                <el-form-item label="">
                    <el-switch v-model="form.cacheEnable"/>
                    <span class="zfile-word-aux"> 60  30 . .</span>
                </el-form-item>

                <el-form-item label="" v-if="form.cacheEnable">
                    <el-switch v-model="form.searchEnable"/>
                    <span class="zfile-word-aux">, </span>
                </el-form-item>

                <el-form-item label="" v-if="form.cacheEnable && form.searchEnable">
                    <el-switch v-model="form.searchIgnoreCase"/>
                </el-form-item>

                <el-form-item label="">
                    <el-select v-model="form.storageStrategy" placeholder="">
                        <el-option label=" OSS" value="aliyun"/>
                        <el-option label=" COS" value="tencent"/>
                        <el-option label=" OBS" value="huawei"/>
                        <el-option label=" KODO" value="qiniu"/>
                        <el-option label=" USS" value="upyun"/>
                        <el-option label="FTP" value="ftp"/>
                        <el-option label="" value="local"/>
                        <el-option label="minio" value="minio"/>
                    </el-select>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="submitForm('form')" round></el-button>
                    <el-button type="danger" @click="clearCache"  round></el-button>
                </el-form-item>
            </el-form>
        </el-col>
    </el-row>
</template>

<script>
    import qs from 'qs';
    export default {
        name: "SiteSetting",
        data() {
            return {
                form: {
                    siteName: '',
                    storageStrategy: '',
                    domain: '',
                    infoEnable: false,
                    searchEnable: false,
                    searchIgnoreCase: false,
                    cacheEnable: false,
                },
                rules: {
                    siteName: [
                        {required: true, message: '', trigger: 'change'},
                    ],
                    domain: [
                        {required: true, type: 'url', message: ',  http://  https:// ', trigger: 'change'},
                    ],
                }
            };
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.$http.post('admin/config', qs.stringify(this.form)).then(() => {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                        })
                    } else {
                        return false;
                    }
                });
            },
            clearCache() {
                this.$http.get('admin/clear-cache').then(() => {
                    this.$message({
                        message: '',
                        type: 'success'
                    });
                })
            }
        },
        mounted() {
            this.$http.get('admin/config').then((response) => {
                this.form = response.data.data;
            })
        }
    }
</script>

<style scoped>
    #siteForm {
        margin-top: 20px;
        margin-left: 20px;
    }

    #siteForm >>> .el-select {
        width: 100%;
    }
</style>